import { BASE_IMAGE_URL,baoxiu } from '@/utils/api'
import mpvueCityPicker from '../../components/mpvueCityPicker.vue'

export default {
	components: {
		mpvueCityPicker
	},
	data() {
		return {
			b_name: '',
			b_phone: '',
			b_content: '',
			b_address: '',
			b_goods_no: ''
		}
	},
	onLoad() {
	},
	methods: {
		async fixContent(){
			if(!this.b_name){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入姓名"
				});
				return;
			};
			
			if(!this.b_phone){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入手机号"
				});
				return;
			};
			
			if(!this.b_content){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入报修内容"
				});
				return;
			};
			
			if(!this.b_goods_no){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入商品编码"
				});
				return;
			};
			
			if(!this.b_address){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "请输入地址"
				});
				return;
			};
			
			let params = {
				b_name: this.b_name,
				b_content: this.b_content,
				b_phone: this.b_phone,
				b_goods_no: this.b_goods_no,
				b_address: this.b_address
			};
			let res = await baoxiu(params);
			
			if(res.code == 1000){
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: "操作成功"
				})
				uni.navigateBack({
					delta: 1
				});
			}
		}
	}
}
